.header-container {
  display: flex;
  justify-content: center;
  height: 140px;
  color: var(--white);
}

.header-parent {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 100%;
  width: 70%;
  color: var(--white);
}

.header-options {
  display: flex;
  align-items: center;
}

.header-option {
  font-size: 16px;
  cursor: pointer;
  font-weight: 600;
}

.header-option-seperator {
  margin: 0 50px 0 0;
  color: var(--white);
}

@media only screen and (max-width: 1110px) {
  .header-parent {
    width: 80%;
  }
}

@media only screen and (max-width: 520px) {
  .header-logo {
    font-size: 35px;
  }
}

@media only screen and (max-width: 970px) {
  .header-hamburger {
    display: block;
  }

  .header-parent {
    width: 100%;
    padding: 0 40px;
    position: relative;
  }

  .header-options {
    display: flex;
    flex-direction: column;
    position: absolute;
    left: -100%;
    top: 0px;
    height: 100vh;
    width: 100%;
    justify-content: space-around;
    z-index: 1000;
    opacity: 0.8;
    transition: all 0.8s ease-out;
  }
}
